}
.Dashboardsidebar.dashboard-sidebarClose{
  width: 80px;
  /* display: none; */
  transition: 0.9s ease-in-out;
  left: 0;
}
.Dashboardsidebar.dashboard-sidebarClose .sideUl .nav-item span {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  clip: rect(0, 0, 0, 0);
  border: 0;
}
.Dashboardsidebar.dashboard-sidebarClose .sideUl .nav-item i {
  font-size: 20px; /* Adjust the size as needed */
}
.Dashboardsidebar.dashboard-sidebarClose .company-logo {
display: none;
}

/* Hide the borders around the doctor's profile */
.Dashboardsidebar.dashboard-sidebarClose .profile-main-box {
  padding-left: 0px;
  padding-right: 0px;
}
.Dashboardsidebar.dashboard-sidebarClose .profile-main-box .doctorprofilesidebar {
  border: none;
}


/* Show only the image of the doctor */
.Dashboardsidebar.dashboard-sidebarClose .doctorprofilesidebar > div {
  display: none;
}

.Dashboardsidebar.dashboard-sidebarClose .doctorprofilesidebar img {
  display: block;
  width: 50px;
  height: 50px;
}
.Dashboardsidebar.dashboard-sidebarClose .dashboard-sidebar-submenu span{
  display: none;
 
}
.Dashboardsidebar.dashboard-sidebarClose .dashboard-sidebar-submenu i{
  font-size: 20px;
 
}



