.bounce {
  animation: bounce 3s infinite;
}

@keyframes bounce {
  0%, 100% { 
    animation-timing-function: ease-in-out;
    transform: translateY(0);
  }
  50% {
    animation-timing-function: ease-in-out;
    transform: translateY(-20px);
  }
}
